
import fs from 'fs';
import path from 'path';
import Papa from 'papaparse';
import {
  transformRawDataArray,
  applyRevenueAdjustment,
  calculateInstitutionStats,
  CourseData
} from '@/lib/data-utils';
import InstitutionAnalysisClient from './InstitutionAnalysisClient';

async function loadData() {
  // Correctly resolve the path to the CSV file from the project root.
  const csvFilePath = path.join(process.cwd(), '..', 'result_kdtdata_202506.csv');
  const csvFile = fs.readFileSync(csvFilePath, 'utf8');
  
  const rawData = Papa.parse(csvFile, {
    header: true,
    skipEmptyLines: true,
  }).data;

  // 1. Transform raw data
  const transformedData = transformRawDataArray(rawData as any[]);

  // 2. Apply revenue adjustments
  const validForOverall = transformedData.filter(c => (c['수료인원'] ?? 0) > 0 && (c['수강신청 인원'] ?? 0) > 0);
  let overallCompletionRate = 0;
  if (validForOverall.length > 0) {
    const total = validForOverall.reduce((sum, c) => sum + (c['수료인원'] ?? 0), 0);
    const enroll = validForOverall.reduce((sum, c) => sum + (c['수강신청 인원'] ?? 0), 0);
    overallCompletionRate = enroll > 0 ? (total / enroll) * 100 : 0;
  }
  const adjustedData = applyRevenueAdjustment(transformedData, overallCompletionRate);

  // 3. Calculate initial statistics
  const initialInstitutionStats = calculateInstitutionStats(adjustedData);

  // 4. Get available years
  const years = new Set(adjustedData.map(d => new Date(d.과정시작일).getFullYear()));
  const availableYears = Array.from(years).sort((a, b) => b - a);

  return {
    initialInstitutionStats,
    availableYears,
    originalData: adjustedData,
  };
}

export default async function InstitutionAnalysisPage() {
  const { initialInstitutionStats, availableYears, originalData } = await loadData();

  return (
    <InstitutionAnalysisClient
      initialInstitutionStats={initialInstitutionStats}
      availableYears={availableYears}
      originalData={originalData}
    />
  );
}
